h1,
h2,
h3,
h4,
p {
    margin-top: 0;
    margin-bottom: 0;
}

a {
    text-decoration: none;
    outline: 0;
    font-style: normal;
}

ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

button {
    cursor: pointer;
    outline: 0;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

* {
    margin: 0;
    padding: 0;
}